import { faMapMarkerAlt, faMobileAlt, faEnvelope, faBirthdayCake } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { Component } from 'react'

import Content from '../../Data/About'

import Section from './Section'

export default class PdfContact extends Component {
  render() {
    return (
      <Section className="pagePdf__contact">
        <div className="row">
          <div className="col-36 col-lg-9">
            <h2 className="pagePdf__subTitle">Contact</h2>
          </div>

          <div className="col-36 col-lg-27 row">
            <div className="col-18 mb-3">
              <FontAwesomeIcon icon={faMapMarkerAlt} fixedWidth={true} />
              <span className="ml-1">{Content.contact.address}</span>
            </div>

            <div className="col-18 mb-3">
              <FontAwesomeIcon icon={faMobileAlt} fixedWidth={true} />
              <a href={`tel:${Content.contact.phone}`} className="ml-1">
                {Content.contact.phone}
              </a>
            </div>

            <div className="col-18">
              <FontAwesomeIcon icon={faBirthdayCake} fixedWidth={true} />
              <span className="ml-1">{Content.contact.birthday}</span>
            </div>

            <div className="col-18">
              <FontAwesomeIcon icon={faEnvelope} fixedWidth={true} />
              <a href={`mailto:${Content.contact.email}`} className="ml-1">
                {Content.contact.email}
              </a>
            </div>
          </div>
        </div>
      </Section>
    )
  }
}
