import React, { Component } from 'react'

import Content, { InterfaceProject } from '../../Data/Project'

import Section from './Section'

export default class PdfProject extends Component {
  render() {
    return (
      <Section className="pagePdf__project">
        <div className="row">
          <div className="col-36 col-lg-9 mb-3 mb-lg-0">
            <h2 className="pagePdf__subTitle">Project</h2>
          </div>
          <div className="col-36 col-lg-27 mb-3">{this.getGroupCardContent()}</div>
        </div>
      </Section>
    )
  }

  getGroupCardContent() {
    return <div id="pagePdf__projectContent">{Content.map((item, index) => this.getSimpleContent(item, index))}</div>
  }

  getSimpleContent(project: InterfaceProject, i: number) {
    return (
      <div className="pagePdf__projectContentItem" key={`pagePdf__projectContentItem--${i}`}>
        <div className="card">
          <div className="card-header">{project.name}</div>
          <div className="card-body">
            <table className="table table-border table-sm w-100">
              <tbody>
                <tr>
                  <th>Description</th>
                  <td>{project.description}</td>
                </tr>
                <tr>
                  <th>Size</th>
                  <td>{project.teamSize}</td>
                </tr>
                <tr>
                  <th>Position</th>
                  <td>{project.position}</td>
                </tr>
                <tr>
                  <th>Responsibilities</th>
                  <td>
                    <ul className="row">{this.getListItem(project.responsibility)}</ul>
                  </td>
                </tr>
                <tr>
                  <th>Technologies</th>
                  <td>
                    <ul className="row">{this.getListItem(project.technology)}</ul>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    )
  }

  getListItem(data: string[]) {
    return data.map((item, index) => (
      <li key={`projectItem--${index}`} className="col-md-12">
        {item}
      </li>
    ))
  }
}
