import React, { Component, ComponentClass } from 'react'
import ReactGA from 'react-ga'
import { RouteComponentProps } from 'react-router'
import { Switch, Route, Redirect, withRouter } from 'react-router-dom'
import { CSSTransition, TransitionGroup } from 'react-transition-group'

import ConfigRouter from '../Config/Router'
import PageAbout from '../Page/About'
import PageContact from '../Page/Contact'
import PageHome from '../Page/Home'
import PageProject from '../Page/Project'
import PageResume from '../Page/Resume'

const Components: { [page: string]: ComponentClass } = {
  Home: PageHome,
  About: PageAbout,
  Resume: PageResume,
  Project: PageProject,
  Contact: PageContact,
}

class RouterContentTransition extends Component<RouteComponentProps> {
  render() {
    return (
      <TransitionGroup className="page__container clearfix">
        <CSSTransition key={`page__transition--${this.props.location.key}`} classNames="page__transition" timeout={300}>
          <Switch location={this.props.location}>
            {ConfigRouter.map((r, k) => (
              <Route exact key={k} path={r.path} component={Components[r.component]} />
            ))}
            <Redirect from="/*" to="/" />
          </Switch>
        </CSSTransition>
      </TransitionGroup>
    )
  }

  componentDidMount() {
    ReactGA.initialize('UA-170116442-1')
    ReactGA.pageview(window.location.pathname + window.location.search)
  }
}

export default withRouter(RouterContentTransition)
