import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import React, { Component } from 'react'
import { Nav, NavItem } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'

import ListRouter, { InterfaceConfigRouter } from '../Config/Router'

export default class NavigationItem extends Component {
  render() {
    return (
      <Nav className="navigation__mainNav col-36 col-md-12">
        {ListRouter.map((router: InterfaceConfigRouter, i: number) => {
          return (
            <NavItem className="navigation__item" key={`navigation__item--${i}`}>
              <NavLink
                className="nav-link navigation__link"
                activeClassName="active disabled"
                to={router.path}
                id={router.component}
                exact
                strict
              >
                <span className="navigation__icon">
                  <FontAwesomeIcon icon={router.icon} />
                </span>
                <span className="navigation__title">{router.title}</span>
              </NavLink>
            </NavItem>
          )
        })}
      </Nav>
    )
  }
}
